<template>
  <div>
      <div class="main">
        <div class="container">
            <div class="left">
              <a @click="ToContent(item)">
                <el-image :src="item.src"
              lazy
              ></el-image>
              </a>
               </div>

      <div class="right">
          <div class="cont">
           <a href="javascript:;" class="title">
             <h3>{{item.title}}</h3>
           </a>
            <p class="post-excerpt">{{item.Introduction}} </p>
            <p class="bottom">
              <i class="iconfont icon-wendang" style="font-size: 25px;"></i><a href="javascript:;">{{item.category}}</a>
              <i class="iconfont icon-xiaoxi" style="font-size: 25px;"></i><span>{{item.message}}</span>
              <i class="iconfont icon-shijian" style="font-size: 25px;"></i><span>{{item.time}}</span>
            </p>
            <p class="readmore" @click="ReadMore(item)">
              <a href="javascript:;">read More</a>
            </p>

          </div>

      </div>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  name: 'Li-st',
  props: {
    item: {
      type: Object,
      // å£°æ˜æ˜¯å¦å¿…éœ€
      require: true
    }
  },
  methods: {
    // è·³è½¬åˆ°å†…å®¹é¡µé¢çš„å‡½æ•°
    jump (item) {
      this.$store.dispatch('SETBGCIMG', item)
      sessionStorage.setItem('src', item.src)
      sessionStorage.setItem('title', item.title)
      sessionStorage.setItem('Introduction', item.Introduction)

      this.$router.push({
        path: '/content'
      })
    },
    ToContent (item) {
      this.jump(item)
    },
    ReadMore (item) {
      this.jump(item)
    }
  }
}
</script>
